.result {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #efefef;
  cursor: pointer;
}
.result:hover {
  background-color: #efefef;
}
.cover {
  width: 12%;
}
.cover img {
  width: 100%;
}
.text {
  width: 87%;
  font-size: 0.7rem;
}
.title {
  font-size: 0.8rem;
  font-weight: 700;
}

/* darkMode */
.dark.result {
  border-bottom: 1px solid #272C38;
}

.dark.result:hover {
  background-color: #272C38;
}
